
















































































import { Component, Vue } from "vue-property-decorator"

import { $Pages } from "@/myStore"
import { $Validator, $Obstacle } from "@/plugins"

@Component({
    computed: {
        error: () => $Pages.$mailer.error,
        success: () => $Pages.$mailer.success
    }
})
export default class Contact extends Vue {
    $refs!: {
        msg
        send
    }
    name = ''
    email = ''
    subject = ''
    msg = ''

    name_err = ''
    email_err = ''
    subj_err = ''
    msg_err = ''

    setMsg (e) {
        this.msg = e.target.textContent
        if (this.msg_err)
        {
            this.msg_err = ''
        }
    }

    reCAPTCHA () {

    }

    send () {
        const schema = [
            {
                fieldName: 'Email',
                data: this.email,
                rules: {
                    required: true,
                    email: true,
                }
            },
            {
                fieldName: 'Name',
                data: this.name,
                rules: {
                    required: true,
                    string: true,
                    min: 3,
                    max: 50
                }
            },
            {
                fieldName: 'Subject',
                data: this.subject,
                rules: {
                    required: true,
                    string: true,
                    min: 3,
                    max: 50
                }
            },
            {
                fieldName: 'Message',
                data: this.msg,
                rules: {
                    required: true,
                    string: true,
                    min: 50,
                    max: 4000
                }
            }
        ]

        let _this = this
        if ($Validator.validate(schema))
        {
            /* "grecaptcha" is loaded directly from scripts */
            // grecaptcha.ready(function () {
            //     grecaptcha.execute('reCAPTCHA_site_key', { action: 'submit' }).then(function (token) {
            //         // Add your logic to submit to your backend server here.
            //         console.log(token)
            $Obstacle.create(this.$refs.send, {
                action: function () {
                    $Pages.$mailer.send({
                        name: _this.name,
                        email: _this.email,
                        subject: _this.subject,
                        message: _this.msg
                    }).then(done => {
                        $Obstacle.destroy(_this.$refs.send)
                        if (done)
                        {
                            _this.name = _this.email = _this.subject = _this.msg = ''
                            _this.$refs.msg.textContent = ''
                        }
                    })
                }
            })
            //     }).catch(() => {
            //         $Pages.$mailer.error = 'connection error!'
            //     })
            // })

        }
        const errors = $Validator.getErrors()
        this.email_err = errors[ 'Email' ]
        this.name_err = errors[ 'Name' ]
        this.subj_err = errors[ 'Subject' ]
        this.msg_err = errors[ 'Message' ]

    }
}
